'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { ChevronRight, AlertCircle, Users, CheckCircle2, Plus, MapPin } from 'lucide-react';

const mockReports = [
  { id: 101, category: 'Damaged turf / turf runoff', severity: 'High', note: 'Blackish runoff after weekend game.', reporter: 'Anita R.', zone: 'Zone P1' },
  { id: 102, category: 'Stagnant water', severity: 'Medium', note: 'Stagnant pool near drainage, mosquitoes.', reporter: 'Ravi K.', zone: 'Zone P1' },
];

export default function UserHome() {
  const [showOnboarding, setShowOnboarding] = useState(true);

  useEffect(() => {
    const hasSeenOnboarding = localStorage.getItem('peep-onboarding-seen');
    if (hasSeenOnboarding) {
      setShowOnboarding(false);
    }
  }, []);

  const handleStartReporting = () => {
    localStorage.setItem('peep-onboarding-seen', 'true');
    setShowOnboarding(false);
  };

  if (showOnboarding) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-white to-gray-50 flex flex-col items-center justify-center px-4 py-8" style={{ backgroundColor: 'var(--peep-neutral-1)' }}>
        <div className="w-full max-w-sm text-center space-y-6">
          {/* Onboarding headline */}
          <div>
            <h1 className="text-3xl font-bold mb-2 text-balance" style={{ color: 'var(--peep-primary)' }}>PEEP — Public Environmental Evidence Portal</h1>
            <p className="text-gray-600 text-base">Report land, soil, or water pollution in seconds. Start with our turf pilot — works anywhere there's land.</p>
          </div>

          {/* Benefits */}
          <div className="space-y-3">
            <div className="flex gap-3 items-start bg-white p-3 rounded-lg border border-gray-200">
              <AlertCircle className="w-5 h-5 flex-shrink-0 mt-0.5" style={{ color: 'var(--peep-primary)' }} />
              <p className="text-sm text-gray-700">Create public evidence cards</p>
            </div>
            <div className="flex gap-3 items-start bg-white p-3 rounded-lg border border-gray-200">
              <Users className="w-5 h-5 flex-shrink-0 mt-0.5" style={{ color: 'var(--peep-primary)' }} />
              <p className="text-sm text-gray-700">Make spots visible to authorities</p>
            </div>
            <div className="flex gap-3 items-start bg-white p-3 rounded-lg border border-gray-200">
              <CheckCircle2 className="w-5 h-5 flex-shrink-0 mt-0.5" style={{ color: 'var(--peep-primary)' }} />
              <p className="text-sm text-gray-700">Join local cleanup actions</p>
            </div>
          </div>

          {/* CTA buttons */}
          <div className="space-y-2 pt-4">
            <Link
              href="/peep/user/map"
              onClick={handleStartReporting}
              className="block w-full py-3 px-6 rounded-2xl font-semibold text-white shadow-lg transition-all active:scale-95"
              style={{ backgroundColor: 'var(--peep-primary)' }}
            >
              Report Pollution
            </Link>
            <button
              onClick={() => setShowOnboarding(false)}
              className="block w-full py-2 px-4 rounded-lg text-gray-700 border border-gray-300 hover:bg-gray-50"
            >
              Learn More
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-4 px-4 py-4">
      <Link
        href="/peep/user/map"
        className="flex items-center justify-center gap-2 w-full py-4 px-6 rounded-2xl font-semibold text-white shadow-lg transition-all active:scale-95"
        style={{ backgroundColor: 'var(--peep-primary)' }}
      >
        <MapPin className="w-5 h-5" />
        Report Pollution
      </Link>

      {/* Nearby reports card */}
      <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
        <div className="p-4 bg-gradient-to-r from-green-50 to-blue-50">
          <h2 className="font-semibold text-gray-800">Nearby Reports</h2>
        </div>
        <div className="divide-y">
          {mockReports.length > 0 ? (
            mockReports.map((report) => (
              <div key={report.id} className="p-3 hover:bg-gray-50 cursor-pointer flex items-start justify-between">
                <div className="flex-1 min-w-0">
                  <p className="text-sm font-medium text-gray-900">{report.category}</p>
                  <p className="text-xs text-gray-600 truncate">{report.note}</p>
                  <p className="text-xs text-gray-500 mt-1">By {report.reporter} • {report.zone}</p>
                </div>
                <span
                  className="text-xs font-bold rounded-full px-2 py-1 ml-2 flex-shrink-0"
                  style={{
                    backgroundColor: report.severity === 'High' ? '#fca5a5' : report.severity === 'Medium' ? '#fcd34d' : '#c6f6d5',
                    color: report.severity === 'High' ? '#7f1d1d' : report.severity === 'Medium' ? '#78350f' : '#15803d',
                  }}
                >
                  {report.severity}
                </span>
              </div>
            ))
          ) : (
            <div className="p-6 text-center">
              <AlertCircle className="w-8 h-8 mx-auto mb-2 text-gray-400" />
              <p className="text-sm text-gray-600">No reports nearby. Be the first to PEEP!</p>
            </div>
          )}
        </div>
        <Link href="/peep/user/map" className="block p-3 border-t border-gray-200 text-center text-sm font-medium text-blue-600 hover:bg-blue-50">
          View Full Map <ChevronRight className="w-4 h-4 inline" />
        </Link>
      </div>

      {/* Quick stats */}
      <div className="grid grid-cols-3 gap-2">
        <div className="bg-white rounded-lg p-3 border border-gray-200 text-center">
          <p className="text-2xl font-bold" style={{ color: 'var(--peep-primary)' }}>12</p>
          <p className="text-xs text-gray-600">Reports in Area</p>
        </div>
        <div className="bg-white rounded-lg p-3 border border-gray-200 text-center">
          <p className="text-2xl font-bold" style={{ color: 'var(--peep-accent)' }}>3</p>
          <p className="text-xs text-gray-600">High Severity</p>
        </div>
        <div className="bg-white rounded-lg p-3 border border-gray-200 text-center">
          <p className="text-2xl font-bold text-blue-600">5</p>
          <p className="text-xs text-gray-600">Verified</p>
        </div>
      </div>
    </div>
  );
}
